import { useState } from "react";
import ProductCard from "../components/ProductCard";
import CategoryFilter from "../components/CategoryFilter";
import LoginModal from "../components/LoginModal";

const Categories = () => {
  const [filteredProducts, setFilteredProducts] = useState([]);

  const allProducts = [
    {
      id: 5,
      name: " ",
      category: "women",
      price: 2500,
      image: "/images/dress.jpeg",
      description: "    ",
      details: ": 100% . :  ",
      sizes: ["S", "M", "L"],
      colors: ["", "", ""],
    },
    {
      id: 6,
      name: " ",
      category: "women",
      price: 1800,
      image: "/images/blosee.jpg",
      description: "   ",
      details: ": 95% , 5% . :   30C",
      sizes: ["XS", "S", "M"],
      colors: ["", "", ""],
    },
    {
      id: 7,
      name: " ",
      category: "men",
      price: 2200,
      image: "/images/manshirt.jpg",
      description: "  ",
      details: ": 100% . :    40C",
      sizes: ["M", "L", "XL", "XXL"],
      colors: ["", "", ""],
    },
    {
      id: 8,
      name: " ",
      category: "kids",
      price: 900,
      image: "/images/kid.jpeg",
      description: "   ",
      details: ": 100% . :    30C",
      sizes: ["104", "110", "116", "122"],
      colors: ["", "", "", ""],
    },
    {
      id: 9,
      name: " ",
      category: "men",
      price: 3200,
      image: "/images/manjeans.jpeg",
      description: "  ",
      details: ": 98% , 2% . :   30C",
      sizes: ["W30", "W32", "W34", "W36", "W38"],
      colors: ["", ""],
    },
    {
      id: 10,
      name: " ",
      category: "women",
      price: 2100,
      image: "/images/womenshirt.jpg",
      description: " -  ",
      details: ": 70% , 30% . : ",
      sizes: ["S", "M", "L"],
      colors: ["", "", ""],
    },
  ];

  const categories = ["women", "men", "kids"];

  const handleFilter = (category, maxPrice) => {
    let filtered = allProducts;

    if (category) {
      filtered = filtered.filter((p) => p.category === category);
    }

    if (maxPrice) {
      filtered = filtered.filter((p) => p.price <= maxPrice);
    }

    setFilteredProducts(filtered);
  };

  return (
    <main className="categories-page">
      <LoginModal />

      <CategoryFilter categories={categories} onFilter={handleFilter} />

      <section className="products-section">
        <div className="products-grid">
          {(filteredProducts.length ? filteredProducts : allProducts).map(
            (product) => (
              <ProductCard key={product.id} product={product} />
            )
          )}
        </div>
      </section>
    </main>
  );
};

export default Categories;
